@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap";
header {
  width: 1200px;
  background-color: #012c2c;
}

body {
  background-color: #e5e5e5;
}

.section {
  width: 100vw;
  justify-content: center;
  align-items: center;
  margin: 60px 0;
  display: flex;
}

.movies-container {
  max-width: 1200px;
  grid-gap: 30px;
  grid-template-columns: repeat(3, 1fr);
  justify-content: center;
  align-items: center;
  justify-items: center;
  margin: 0;
  display: grid;
}

.info__title {
  font-family: Roboto, sans-serif;
}

.info__genre {
  color: #ff6b08;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 16px;
}

.info__poster--img {
  width: 274px;
  height: 398px;
  border-radius: 5px;
}

.pagination__button {
  width: 40px;
  height: 40px;
  border-radius: 5px;
}

.pagination__svg {
  width: 16px;
  height: 16px;
}

.pagination__page {
  flex-direction: row;
  display: flex;
}

.pagination__page li {
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.currentPage {
  background-color: #ff6b08;
  border-radius: 5px;
}

#pagination {
  justify-content: center;
  align-items: center;
  display: flex;
}

#footer-bg {
  background-color: #f7f7f7;
}

@media screen and (max-width: 480px) {
  .movies-container {
    grid-template-columns: repeat(1, 1fr);
    display: grid;
  }

  .info__genre {
    line-height: 14px;
  }
}

@media screen and (max-width: 768px) {
  .movies-container {
    grid-template-columns: repeat(2, 1fr);
    display: grid;
  }
}

